*{
    box-sizing: border-box;
    font-family: "rubik", sans-serif;
    color: black;
    padding: 0;
    margin: 0;
}
body{
    width: 100%;
    overflow-x: hidden;
}

@keyframes animateDown {
0%,20%,50%,80%,100%{
    transform: translateY(0);
}
40%{
    transform: translateY(5px);
}
60%{
    transform: translateY(3px);
} 
}

a{
    text-decoration: none;
}